.side-bar {
    background-color: black;
    width: 150px;
    height: 100%;
    position: fixed;
    top: 0;
    /*remove margin top*/
    left: 0;
    /*remove margin left*/
    overflow: hidden;
    /* hide over flow of children*/
    transition: 0.5s;
}

.side-bar a {
    display: block;
    background-color: grey;
    padding: 12px 16px;
    text-decoration: none;
    color: white;
}

.side-bar .close-btn {
    text-align: right;
}

h1 {
    color: rgb(8, 8, 8);
    font-size: 32px;
    text-align: center;
    -webkit-animation-name: news;
    -webkit-animation-duration: 3s;
    animation: pulse 3s infinite;
    animation-iteration-count: infinite;
    animation-name: patients;
    animation-duration: 3s infinite;
}
@-webkit-keyframes patients {
    from {
        background-color: rgb(15, 38, 109);
    }
    to {
        background-color: rgb(27, 100, 57);
    }
}

@keyframes patients {
    from {
        background-color: rgb(15, 38, 109);
    }
    to {
        background-color: rgb(27, 100, 57);
    }
}

.call-outs-container {
    max-width: 1400px;
